.image-escalators {
	@extend %pos-abs;
	top: 0;
	right: 0;
	z-index: -1;
	background: url('../assets/images/pink-stairs-big.jpg') no-repeat;
	background-size: cover;
	background-position: top center;
	width: 90rem;
	height: 85rem;
	border-bottom-left-radius: 90%;
	transform-origin: right top;

	@include device(tab-land) {
		width: 70rem;
		height: 80rem;
	}
	@include device(tab) {
		width: 100%;
		height: 30vw;
		background: url('../assets/images/pink-stairs-zoom.jpg') no-repeat;
		background-size: cover;
		border-bottom-left-radius: 40vw;
	}

	@include device(phone) {
		height: 32vw;
	}
	@include device(phone-ant) { height: 35vw; border-bottom-left-radius: 30vw; }
}